0CAAA;EAEA,yBN0BK;EMzBL,6CNUe;EMTf,mBNeU;EMbV,uBAAA;AX4XJ;;AW1XE;EACE,6CAAA;EACA,mBAAA;AX6XJ;;AYjbA;EACI,aAAA;EACA,WAAA;EAEA,kBAAA;EAEA,6CPmDc;EOlDd,wCAAA;AZkbJ;AY5aI;EACE,sCAAA;EACA,uCAAA;AZmbN;AYjbM;EACE,uCAAA;EACA,uCAAA;AZmbR;AY9aI;EACE,oCPvBC;EOyBD,4CAAA;EACA,gBAAA;EACA,gBAAA;EACA,cPZmB;EOcnB,gBAAA;EACA,mBAAA;AZ8aN;;Aa/cA;EACI,kBAAA;EAEA,aAAA;EACA,sBAAA;EAEA,WAAA;EACA,gBAAA;AbgdJ;Aa9cI;EACE,kBAAA;EACA,SAAA;EACA,UAAA;EACA,aAAA;EACA,eAAA;EACA,UAAA;AbgdN;Aa7cI;EAEE,gBAAA;EAEA,aAAA;EACA,YAAA;EACA,wCAAA;EAEA,cRTa;EQWb,iBAAA;EACA,oBAAA;EACA,kBAAA;EACA,mBAAA;Ab2cN;AazcM;EACE,cRdgB;ALydxB;;AarcE;EAEE,aAAA;EACA,sBAAA;EAEA,WAAA;EACA,gBAAA;AbscJ;AapcI;EACE,gBAAA;EAEA,aAAA;EACA,YAAA;EACA,wCAAA;EAEA,cRtCa;EQwCb,kBAAA;EAEA,kBAAA;AbkcN;AahcM;EACE,cR1CgB;AL4exB;AchgBA;ENqCE;IAsCI,8BAAA;IAAA,sBAAA;IACA,cHzCsB;IG0CtB,yBHzCwB;ELmT5B;EQzQI;IACE,6BAAA;IACA,cH7CoB;IG8CpB,yBH7CsB;ELwT5B;EW5VF;IAKM,eAAA;EXwYJ;EY7YF;IAUM,gBAAA;EZmbJ;AA+DF","sourcesContent":["\n.container {\n  width: 100%;\n  margin: 0 auto;\n\n  padding-top: 0;\n  padding-bottom: 0;\n  padding-left:  clamp(12px, 0.344rem + 1.84vw, 32px);\n  padding-right: clamp(12px, 0.344rem + 1.84vw, 32px);\n\n  max-width: 1440px;\n\n  // max-width: clamp(352px, -0.617rem + 97vw, 1376px)\n\n  // @include mobile {\n  //   max-width: 376px;\n  // }\n\n  // @include tablet {\n  //   max-width: 768px;\n  // }\n\n  // @include desktop {\n  //   padding: 0 32px;\n  //   max-width: 1440px;\n  // }\n}","@import '~normalize.css';\n\n\n\n// utils\n@import '../../style/utils/mixin';\n@import '../../style/utils/container';\n@import '../../style/utils/fonts';\n@import '../../style/utils/is-hidden';\n@import '../../style/utils/reset';\n@import '../../style/utils/vars';\n@import '../../style/common';\n\n// component\n@import '../../style/components/index.scss';\n\n// partials\n\n","@font-face {\n  font-family: \"M PLUS 1p\";\n  src: url('../../fonts/MPLUS1p-Regular.ttf') ;\n    font-weight: 400;\n}\n@font-face {\n  font-family: \"M PLUS 1p\";\n  src: url('../../fonts/MPLUS1p-Medium.ttf') ;\n    font-weight: 500;\n}\n\n@font-face {\n  font-family: \"Russo One\";\n  src: url('../../fonts/Russo_One.ttf') ;\n    font-weight: 400;\n}\n\n\n  // @font-face {\n  //   font-family: 'Roboto';\n  //   src: url('../../fonts/Roboto-Regular.woff') format('woff'),\n  //     url('../../fonts/Roboto-Regular.woff2') format('woff2');\n  //   font-weight: 400;\n  // }",".is-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    margin: -1px;\n    border: 0;\n    padding: 0;\n\n    white-space: nowrap;\n    clip-path: inset(100%);\n    clip: rect(0 0 0 0);\n    overflow: hidden;\n}","*{\n    box-sizing: border-box;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\nimg {\n    display: block;\n    max-width: 100%;\n    height: auto;\n}\n\na {\n    text-decoration: none;\n    color: currentColor;\n    display: block;\n}\n\nul {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n.link {\n    text-decoration: none;\n}\n\n.list {\n    text-decoration: none;\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\nsvg{\n    fill: currentColor;\n}\n","html,\nbody {\n  font-family: $main;\n  background-color: $main-bg;\n\n  font-size: clamp(18px, 0.5rem + 1.2vw, 20px);\n  font-weight: 400;\n  line-height: clamp(24px, 0.8rem + 1.5vw, 28px);\n  color: $main-text;\n  \n  margin: 0 auto;\n  width: 100%;\n\n  scroll-behavior: smooth;\n}\n//   clamp(min, current, max)\n//   window 1440px / max font-size = 44px\n//   1vw = 14.4px\n//   44px / 14.4px = 3.05vw\n\naddress {\n  font-style: normal;\n}\n\n// link hover focus active \n.link-hfa{\n  transition: $transition;\n\n  &:hover,&:focus{\n    color: $hover-accent-text;\n    & svg{\n      transition: $transition;\n      color: $hover-accent-text;\n    }\n  }\n  &:active{\n    color: $active-accent-text;\n\n    & svg{\n      transition: $transition;\n      color: $active-accent-text;\n    }\n  }\n}\n\n\n.bg-card{\n  background-color: $card-bg;  \n}\n\n.br-16-to-24{\n  border-radius: clamp(16px, 0.6rem + 1.1vw, 24px);\n}\n\n.br-24{\n  border-radius: 24px;\n}\n\n.br-24-to-20{\n  border-radius: clamp(20px, 1.7rem - 1.5vw, 24px);\n}\n\n// width height \n.w-32-to-48{\n  width: clamp(32px, 1rem + 4vw, 48px);\n}\n.h-32-to-48{\n  height: clamp(32px, 1rem + 4vw, 48px);\n}\n\n\n\n// padding margin\n.p-lr-12-to-32{\n  padding-left: clamp(12px, 0.125rem + 2.3vw, 32px);\n  padding-right: clamp(12px, 0.125rem + 2.3vw, 32px);\n}\n.p-lr-12-56{\n    padding-left: clamp(12px, -0.4rem + 4vw, 56px);\n    padding-right: clamp(12px, -0.4rem + 4vw, 56px);\n}\n\n.p-tb-12-to-16{\n  padding-top: clamp(12px, 0.625rem + 0.5vw, 16px);\n  padding-bottom: clamp(12px, 0.625rem + 0.5vw, 16px);\n}\n.p-tb-16-to-32{\n  padding-top: clamp(16px, 0.4rem + 1.9vw, 32px);\n  padding-bottom: clamp(16px, 0.4rem + 1.9vw, 32px);\n}\n\n.mb-24-to-32{\n  margin-bottom: clamp(24px, 1rem + 1vw, 32px);;\n}\n\n.mb-40-to-32{\n  margin-bottom: clamp(40px, 1.25rem + 1vw, 32px);\n}\n.mb-44-to-68{\n  margin-bottom: clamp(44px, 2rem + 3vw, 68px);\n}\n.mb-88-to-120{\n  margin-bottom: clamp(88px, 3.5rem + 5vw, 120px);\n}\n\n.mt-24-to-32{\n  margin-top: clamp(24px, 1.25rem + 1vw, 32px);;\n}\n\n\n// flex\n.flex {\n  display: flex;\n}\n.fd-row{\n  flex-direction: row;\n}\n.fd-col{\n  flex-direction: column;\n}\n.wrap{\n  flex-wrap: wrap;\n}\n.jc-sb{\n  justify-content: space-between;\n}\n.ai-c{\n  align-items: center;\n}\n.gap-12-to-32{\n  gap: clamp(12px, 0.125rem + 2.5vw, 32px)\n}\n.gap-24-to-32{\n  gap: clamp(24px, 1rem + 1vw, 32px);\n}\n.gap-44-to-68{\n  gap: clamp(44px, 2rem + 4vw, 68px)\n}\n.gap-88-to-120{\n  gap: clamp(88px, 3.5rem + 5vw, 120px);\n}\n\n\n\n\n\n\n\n\n\n\n\n","// font family\n$main:\"M PLUS 1p\", sans-serif;\n$russo:\"Russo One\", sans-serif;\n\n// transition\n$transition: all 0.3s ease-out;\n$transition-burger: all 0.5s ease-out;\n\n// text\n$black:#000;\n$white:#fff;\n$white-text: #F6F7F7;\n$main-text:#C2CDC6;\n\n$hover-accent-text: #F9A207;\n$active-accent-text: #FFD74A;\n\n$input-select-text:#171C1A;\n$about-us-subheader-text:#FFE888;\n$input-label-text:#E1E6E3;\n$input-placeholder-text:#73877B;\n\n// buttons\n$btn-primary-text:#171C1A;\n$btn-primary-bg:#FFC935;\n$btn-primary-accent-bg:#F9A207;\n$btn-primary-active-bg:#FFD74A;\n$btn-primary-disable-bg:#333C38;\n\n\n$btn-secondary-text:#F6F7F7;\n$btn-secondary-accent-text:#171C1A;\n$btn-secondary-border:1px solid $white-text;\n$btn-secondary-accent-border:1px solid #FFD74A;\n$btn-secondary-accent-bg:#FFD74A;\n$btn-secondary-desktop-text:#FFC935;\n$btn-secondary-desctop-border:1px solid #FFC935;\n\n// tabs \n$tab-bg:#5C7065;\n$tab-color-text: #F6F7F7;\n$tab-border:1px solid #5C7065;\n\n$tab-active-text: #5C7065;\n$tab-active-bg:#F6F7F7;\n$tab-active-border: 1px solid #5C7065;\n\n// pagination\n$pagination-bg:#48594F;\n$pagination-text:#F6F7F7;\n$pagination-active-bg:#F6F7F7;\n$pagination-active-text:#48594F;\n\n\n// pref/about-us card\n$pref-card-border: clamp(1px,1vw,2px) solid #73877B;\n$pref-card-circle:#FFD74A;\n$about-us-border: clamp(2px,1vw,1px) solid #73877B;\n\n\n// frames\n$frame-rad-12:12px;\n$frame-rad-16:16px;\n$frame-rad-20:20px;\n$frame-rad-24:24px;\n$frame-rad-40:40px;\n\n// background\n$card-bg:#283935;\n$slider-bg:#3C4942;\n$main-bg:#182825;\n\n\n// calculator\n$calc-type-active-bg: #5C7065;;\n$calc-type-border:1px solid #73877B; \n\n$calc-step-non-active: #5C7065;\n$calc-step-active-text: #171C1A;\n$calc-step-active-bg: #F6F7F7;\n$calc-step-active-border: #F6F7F7;\n$calc-step-svg: #C2CDC6;\n\n// modal\n$modal-bgd: #4f5551b3;\n\n\n\n","h1{\n    font-family: \"Russo One\", sans-serif;\n    font-size: clamp(28px, 1.1rem + 1.8vw, 44px);\n    line-height: clamp(32px, 1.2rem + 1.8vw, 48px);\n    color: $white-text;\n  }\n  h2{\n    font-family: \"Russo One\", sans-serif;\n    font-size: clamp(24px, 0.9rem + 1.8vw, 40px);\n    line-height: clamp(32px, 1.1rem + 1.8vw, 48px);\n    color: $white-text;\n  }\n  h3{\n    font-family: \"Russo One\", sans-serif;\n    font-size: clamp(22px, 0.7rem + 1.8vw, 32px);\n    line-height: clamp(28px, 1.1rem + 1.8vw, 36px);\n    color: $white-text;\n    font-weight: 500;\n  }\n  h4{\n    font-size: clamp(18px, 0.5rem + 2vw, 32px);\n    line-height: clamp(24px, 0.8rem + 2vw, 36px);\n    color: $white-text;\n    font-weight: 500;\n  }\n\n\n  // -------- for component page -------\n  .component-title{\n    margin-top: 40px;\n    margin-bottom: 20px;\n  }\n  .component-container{\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-wrap: wrap;\n    gap: 20px;\n  }\n  .component-list{\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    align-items: center;\n    gap: 10px;\n    width: 100%;\n  }\n\n",".logo-icon{\n    width: clamp(88px, 3.5rem + 6vw, 116px);\n    height: clamp(40px, 1rem + 5vw, 56px)\n  }",".btn-primary{\n\n    padding-top: clamp(16px, 0.65rem + 1vw, 20px);\n    padding-bottom: clamp(16px, 0.65rem + 1vw, 20px);\n    padding-left: clamp(17.5px, 0.5rem + 2.7vw, 40px);\n    padding-right: clamp(17.5px, 0.7rem + 2.3vw, 40px);\n  \n    background-color: $btn-primary-bg;\n    color: $btn-primary-text;\n  \n    border: none;\n    outline: none;\n    border-radius: clamp($frame-rad-12, 2.6vw, $frame-rad-20);\n    \n  \n    font-size: clamp(18px, 0.5rem + 1.5vw, 20px);\n    font-weight: 500;\n    line-height: clamp(24px, 0.8rem + 1.8vw, 28px);\n  \n    cursor:pointer;\n  \n    transition: $transition;\n  \n    &:hover,&:focus{\n      background-color: $btn-primary-accent-bg;\n    }\n    &:active{\n      background-color: $btn-primary-active-bg;\n    }\n    &:disabled{\n      background-color: $btn-primary-disable-bg;\n      color: $white-text;\n      cursor:initial;\n    }\n  \n  }\n  \n  .btn-secondary{\n  \n    padding-top: clamp(16px, 0.65rem + 1vw, 20px);\n    padding-bottom: clamp(16px, 0.65rem + 1vw, 20px);\n    padding-left: clamp(17.5px, 0.5rem + 2.3vw, 24px);\n    padding-right: clamp(17.5px, 0.7rem + 2.3vw, 24px);\n    \n    width: 100%;\n    max-width: 100%;\n  \n    background-color: transparent;\n    color: $white-text;\n  \n    border: $btn-secondary-border;\n    outline: none;\n    border-radius: clamp($frame-rad-12, 2.6vw, $frame-rad-20);\n    \n  \n    font-size: clamp(18px, 0.5rem + 1.5vw, 20px);\n    font-weight: 500;\n    line-height: clamp(24px, 0.8rem + 1.8vw, 28px);\n  \n    cursor:pointer;\n  \n    transition: $transition;\n  \n    &:hover,&:focus{\n      background-color: $btn-secondary-accent-bg;\n      color: $btn-secondary-accent-text;\n      border: $btn-secondary-accent-border;\n    }\n    &:active{\n      background-color: transparent;\n      color: $white-text;\n      border: $btn-secondary-border;\n    }\n  \n    @include tablet{\n      max-width: max-content;\n      color: $btn-secondary-desktop-text;\n      border: $btn-secondary-desctop-border;\n      &:active{\n        background-color: transparent;\n        color: $btn-secondary-desktop-text;\n        border: $btn-secondary-desctop-border;\n      }\n    }\n  }",".tab-label{\n    display: inline-block;\n    padding: 8px 12px;\n  \n    border-radius: $frame-rad-12;\n    border: $tab-border;\n    outline: none;\n  \n    background-color: $tab-bg;\n  \n    transition: $transition;\n  }\n  \n  .tab-checkbox{\n    display: none;\n  }\n  .tab-checkbox:checked + .tab-label {\n    background-color: $tab-active-bg;\n    color: $tab-active-text;\n    border: $tab-active-border;\n  }",".pagination-item{\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  \n    // font-size: clamp(18px, 0.5rem + 1.5vw, 20px);\n    font-size: 20px;\n    font-weight: 400;\n    // line-height: clamp(24px, 0.8rem + 1.8vw, 28px);\n    line-height: 1.4;\n  \n    color: $pagination-text;\n    background-color: $pagination-bg;\n    border-radius: $frame-rad-40;\n  \n    // width: clamp(48px, 1.5rem + 4vw, 60px);\n    // height: clamp(48px, 1.5rem + 4vw, 60px);\n    width: 60px;\n  